import BaseStore from './BaseStore'

class HackerNewsStore extends BaseStore {
  fetchItem = id =>
    this.fetch(`https://hacker-news.firebaseio.com/v0/item/${id}.json`)
      .then(response => (this.data = response.data))
      .then(() => this.emit('change'))
      .catch(err => {
        console.error('[HackerNewsStore#fetchItem]', err)
      })

  onEvent(event) {
    switch (event.type) {
      case 'HACKER_NEWS_FETCH_ITEM': {
        this.fetchItem(event.id)
      }
    }
  }
}

export default new HackerNewsStore()



